@import "styles.css";

.photography {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 2rem;
}

.gallery {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 1rem;
  margin-bottom: 1rem;
  margin-left: 1rem;
  margin-right: 1rem;
}

.gallery-spot {
  width: 47.9%;
  margin: 0.5rem;
}


@media only screen and (max-width: 900px) {
  .gallery-spot {
    width: 100%
  }
}

.photo {
  border-radius: 9px;
  height: auto;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.location {
  color: var(--white);
  opacity: 0;
  position: relative;
  text-align: center;
  border-bottom-left-radius: 9px;
  border-bottom-right-radius: 9px;
  top: -1.40rem;
  background-color: #343a40;
}

.gallery-spot:hover .location {
  opacity: 0.8;
}
