/* Global variables */
:root {
    --background-white: #F8F9FA;
    --white: #FFFFFF;
    --darker-white: #DEE2E6;
    --links-color: #538D22;
    --black: #212529;
    --header-background: #E9ECEF;
    --green: #15BE53;
    --blue: #3E92CC;
    --orange: #DD6E42;
    --cyan: #4F6D7A;
    --code-color: #9E2A2B;
    --paper-color: #335C67;
    --bullet-color: #9E2A2B;
}

* { 
    box-sizing: border-box; 
}

body {
  margin: 0;
  color: var(--black);
  background-color: var(--background-white);
}

.nav {
  background-color: var(--background-white);
  opacity: 0.97;
  color: var(--black);
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  padding: 0.85rem 2rem;
  position: sticky;
  top: 0;
  z-index: 1;
}

.site-title {
  font-size: 1.75rem;
}

.site-title:hover {
  opacity: 0.75;
  color: var(--links-color);
}

.nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 1rem;
  font-size: 1.1rem;
}

.nav a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: .25rem;
}

.nav li:hover {
  opacity: 0.75;
  color: var(--links-color);
}

@media (min-width: 50em) {
    .section-header {
        margin-left: 3rem;
    }
}

.section-header {
  color: var(--black);
  border-style: solid;
  border-color: var(--header-background);
  background-color: var(--header-background);
  width: fit-content;
  border-radius: 100px;
  justify-content: center;
  padding: 0.7rem;
  margin-left: 0.5rem;
  margin-bottom: 0.25rem;
  font-size: 1.5rem;
}

.links {
  color: var(--links-color);
  font-weight: 600;
  text-decoration: none;
}

.links:hover {
  opacity: 0.85;
  color: var(--black);
}
