@import "styles.css";

.home-intro {
    display: grid;
    gap: 1.5rem; 
}

.home-intro-images {
    display: flex;
    flex-direction: column;
    padding-left: 1.7rem;
    padding-right: 1.7rem;
    padding-top: 1.7rem;
}

.home-intro-images-profile-picture {
    border-radius: 9px;
    width: 100%;
}

.home-intro-images-icons {
    display: flex;
    margin-top: 1rem;
    position: relative;
}

.home-intro-writing {
    padding-right: 1.7rem;
    padding-left: 1.7rem;
    padding-top: 0.2rem;
    width: 100%;
    align-items: center;
}

.home-intro-writing h1 {
    font-size: clamp(2rem, 10vw, 3rem);
}

.home-intro-writing p {
    font-size: clamp(1rem, 10vw, 1.3rem);
}


@media (min-width: 50em) {
    .home-intro {
        grid-template-columns: 0.7fr 1fr;
    }
    
    .home-intro-images {
        padding-left: 3.75rem;
        padding-right: 0.5rem;
    }

    .home-intro-images-profile-picture {
        width: 100%;
    }

    .home-intro-writing {
        padding-right: 6.5rem;
        padding-left: 0.5rem;
        padding-top: 0.2rem;
    }
}

.github-icon {
    width: 30%;
    margin-left: 30%;
    transition: all .2s ease-in-out;
}

.github-icon:hover {
    transform: scale(1.15);
}

.email-icon {
    width: 30%;
    margin-left: 70%;
    transition: all .2s ease-in-out;
}

.email-icon:hover {
    transform: scale(1.15);
}

.google-scholar-icon {
    width: 30%;
    margin-left: -13%;
    transition: all .2s ease-in-out;
}

.google-scholar-icon:hover {
    transform: scale(1.15);
}

.home-news {
  display: flex;
  flex-direction: column;
  background-color: var(--darker-white);
  padding: 1.7rem;
}

.timeline {
  margin-top: 0rem;
  margin-left: 1.7rem;
  margin-right: 1.7rem;
  padding-top: 0rem;
  padding-left: 4rem;
  padding-right: 4rem;
}


.timeline-item {
  display: flex;
  flex-direction: row;
  font-size: 1.3rem;
  margin-left: 1.8rem;
  margin-bottom: 0.5rem;
  padding-top: 0.6rem;
  padding-bottom: 0.6rem;
  padding-left: 0.6rem;
  padding-right: 2rem;
  background-color: #e9ecef;
  border-radius: 9px;
  width: fit-content;
  /*box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);*/
}

.timeline-item .circle {
  border: 2px solid var(--orange); 
  border-radius: 50%;
  min-width: 0.75em;
  min-height: 0.75em;
  width: 0.75em;
  height: 0.75em;
  position: relative;
  top: 0.25rem;
  right: 0.25rem;
}

.timeline-item .date{
  color: var(--cyan);
  font-weight: 700;
  padding-left: 2rem;
}

.timeline-item .news {
  color: var(--black);
  font-weight: 500;
  padding-left: 0.3rem;
}

.timeline-list {
  list-style: none;
  padding-left: 1%;
  margin-top: 0%;
}
.timeline-list >li {
  position: relative;
  display: flex;
  flex-direction: row;
}
.timeline-list >li:before {
  content: '\25CB';
  position: absolute;
  top: 0.75em;
  font-size: 1rem;
  font-weight: bold;
  color: var(--bullet-color);
}
