@import "styles.css";

.publications-section-header {
    text-decoration: none;
    display: grid;
    grid-template-columns: 1fr;
}

.publications-section-header a {
    text-decoration: none;
}


.all-publications-link {
  display: flex;
  flex-direction: row;
  color: var(--black);
  position: relative;
  top: 30%;
  font-size: 1.1rem;
  margin-bottom: 1rem;
  padding-left: 3rem;
  justify-content: left;
  align-items: left;
}

.all-publications-link .arrow {
  transition: 0.2s ease-in;
}

.all-publications-link:hover .title {
  opacity: 0.65;
}

.all-publications-link:hover .arrow {
  opacity: 0.65;
  transform: translateX(50%);
}

.publications-section a {
  text-decoration: none;
}

.research {
    padding-left: 0.1rem;
    padding-right: 0.1rem;

}

@media (min-width: 30em) {
    .all-publications-link {
        align-items: center;
        justify-content: center;
        padding-left: 1rem;
    }

    .publications-section-header {
        grid-template-columns: 1fr 0.7fr;
    }
}

@media (min-width: 50em) {
    .research {
        padding-left: 3.75rem;
        padding-right: 3.75rem;
    }
    .publications-list {
        margin-left: 1.7rem;
        margin-right: 1.7rem;
        padding-right: 3.7rem;
    }
}

.publications-list > li {
  position: relative;
  display: flex;
  flex-direction: row;
}

.publications-list > li:before {
  content: '\25CB';
  position: absolute;
  top: 0.75em;
  font-size: 1rem;
  font-weight: bold;
  color: var(--bullet-color); 
}

.publications-list {
    margin-left: 0.5rem;
    margin-right: 0.5rem;
}

.publications {
  padding-left: 2rem;
  margin-top: 0.61rem;
  font-size: 1rem;
}

p.publication-info {
  margin-top: 0.05%;
}

.publication-link {
  text-decoration: none;
  color: var(--white);
}

.venue-type {
    border-style: solid;
    border-radius: 7px;
    text-align: center;
    width: fit-content;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
}

.venue-type:hover {
    background-color: #3A5A40;
    border-color: #3A5A40;
}

.paper {
    border-color: var(--paper-color);
    background-color: var(--paper-color);
}

.code {
    border-color: var(--code-color);
    background-color: var(--code-color);
}
